<template>
  <div v-nav="{title: '面板'}">
    <panel header="文字组合列表" :indent="false">
      <panel-item title="标题一" desc="由各种物质组成的巨型球状天体，叫做星球。星球有一定的形状，有自己的运行轨道。" :link="{path: '/demo/panel'}"></panel-item>
      <panel-item title="标题二" desc="由各种物质组成的巨型球状天体，叫做星球。星球有一定的形状，有自己的运行轨道。" :link="{path: '/demo/panel'}"></panel-item>
    </panel>
    <panel header="图文组合列表">
      <panel-item :img-src="img" title="标题一" desc="由各种物质组成的巨型球状天体，叫做星球。星球有一定的形状，有自己的运行轨道。" :link="{path: '/demo/panel'}"></panel-item>
      <panel-item :img-src="img" title="标题二" desc="由各种物质组成的巨型球状天体，叫做星球。星球有一定的形状，有自己的运行轨道。" :link="{path: '/demo/panel'}"></panel-item>
    </panel>
    <panel :header="header" footer="查看更多" :more-link="{path: '/demo/panel'}">
      <panel-item :img-src="img" title="标题一" desc="由各种物质组成的巨型球状天体，叫做星球。星球有一定的形状，有自己的运行轨道。"></panel-item>
    </panel>
    <panel :header="header" footer="查看更多" :more-link="{path: '/demo/panel'}" :card="true">
      <panel-item :img-src="img" title="标题一" desc="由各种物质组成的巨型球状天体，叫做星球。星球有一定的形状，有自己的运行轨道。"></panel-item>
    </panel>
  </div>
</template>

<script>
import { Panel, PanelItem } from '../components/panel'

export default {
  components: {
    Panel,
    PanelItem
  },
  data () {
    return {
      header: '<span style="padding:0 2px;border:1px solid #F53D3D;color:#F53D3D;border-radius:2px;">置顶</span>',
      img: 'data:image/png;base64,'
    }
  }
}
</script>



