<template>
  <div v-nav="{title: 'Toast'}">
    <list :indent="false" title="插件模式(推荐)">
      <list-item @on-click="toast1()">简单模式（text模式）</list-item>
      <list-item @on-click="toast2('top')">顶部显示</list-item>
      <list-item @on-click="toast2('bottom')">底部显示（默认）</list-item>
      <list-item @on-click="toast2('middle')">居中显示</list-item>
      <list-item @on-click="toast2('default')">自由显示</list-item>
      <list-item @on-click="toast3('success', '提交成功')">成功提示</list-item>
      <list-item @on-click="toast3('cancel', '取消成功')">取消提示</list-item>
      <list-item @on-click="toast3('warn', '提交失败')">警告提示</list-item>
      <list-item @on-click="toast3('text', '文本内容')">文本提示（默认）</list-item>
      <list-item @on-click="toast4()">设置消息框长度</list-item>
      <list-item @on-click="toast5()">设置消息框停留时长3s(默认2s)</list-item>
    </list>
  </div>
</template>

<script>
import { List, ListItem } from '../components/list'

export default {
  components: {
    List,
    ListItem
  },
  methods: {
    toast1 () {
      this.$toast.show('网络链接错误')
    },
    toast2 (position) {
      this.$toast.show({text: '网络链接错误', position: position})
    },
    toast3 (type, text) {
      this.$toast.show({text: text, position: 'middle', type: type})
    },
    toast4 () {
      this.$toast.show({text: '网络链接错误', width: '220px'})
    },
    toast5 () {
      this.$toast.show({text: '网络链接错误', time: 3000})
    }
  }
}
</script>



