<template>
  <div v-nav="{title: 'Tooltip'}">
    <list :indent="false" title="插件模式(推荐)">
      <list-item @on-click="show1()">常规</list-item>
      <list-item @on-click="show2()">设置时长</list-item>
      <list-item @on-click="show3()">自定义class</list-item>
    </list>
  </div>
</template>

<script>
import { List, ListItem } from '../components/list'

export default {
  components: {
    List,
    ListItem
  },
  methods: {
    show1 () {
      this.$tooltip.show('错误提示')
    },
    show2 () {
      this.$tooltip.show({content: '必填项不能为空', duration: 3000})
    },
    show3 () {
      this.$tooltip.show({content: '错误提示', className: 'top-44'})
    }
  }
}
</script>

<style>
.weui-toptips {
  top: 44px;
}
.top-44 {
  top: 44px;
}
</style>



