<template>
  <div v-nav="{title: '文本框'}">
    <div id="form">
      <group type="form" title="文本域">
        <x-textarea title="姓名" placeholder="请输入申请原因" required :max-length="200"></x-textarea>
      </group>
      <group type="form" title="不显示计数器">
        <x-textarea title="姓名" placeholder="请输入申请原因" required :show-counter="false"></x-textarea>
      </group>
      <group type="form" title="自动行高（porp:autosize）">
        <x-textarea title="姓名" placeholder="请输入申请原因" required autosize :max-length="200"></x-textarea>
      </group>
    </div>
    <div style="padding:30px 20px">
      <x-button color="primary" @click.native="submit">提交</x-button>
    </div>
  </div>
</template>

<script>
import Group from '../components/group'
import XTextarea from '../components/textarea'
import XButton from '../components/button'

export default {
  components: {
    Group,
    XTextarea,
    XButton
  },
  data () {
    return {
    }
  },
  methods: {
    submit () {
      this.$form.validate('#form', this, (error) => {
        if (!error) {
          this.$toast.show('校验成功')
        }
      })
    }
  }
}
</script>

<style>
.weui-toptips {
  top: 44px !important;
}
</style>



