<template>
  <div v-nav="{title: '单选框'}">
    <div id="form">
      <group type="radio" title="普通">
        <x-radio v-model="v1" required :options="options" name="radio1" empty-tips="请选择性别"></x-radio>
      </group>
      <group type="radio" title="默认选中">
        <x-radio v-model="v2" :options="options" name="radio2" ></x-radio>
      </group>
      <group type="radio" title="不可选">
        <x-radio v-model="v3" required :options="options2" name="radio3" empty-tips="请选择学历"></x-radio>
      </group>
      <group type="radio" title="带图标">
        <x-radio v-model="v4" :options="options3" name="radio4"></x-radio>
      </group>
    </div>
    <div style="padding:30px 20px">
      <x-button color="primary" @click.native="submit">提交</x-button>
    </div>
  </div>
</template>

<script>
import Group from '../components/group'
import XRadio from '../components/radio'
import XButton from '../components/button'

export default {
  components: {
    Group,
    XRadio,
    XButton
  },
  data () {
    return {
      options: [
        {label: '男', value: 'male'},
        {label: '女', value: 'female'}
      ],
      options2: [
        {label: '本科', value: '1'},
        {label: '硕士', value: '2'},
        {label: '博士', value: '3'},
        {label: '其他 (不可选)', value: '4', disabled: true}
      ],
      options3: [
        {label: '选项1', value: '10', icon: 'data:image/png;base64,'},
        {label: '选项2', value: '20', icon: 'data:image/png;base64,'}
      ],
      v1: '',
      v2: 'male',
      v3: '',
      v4: ''
    }
  },
  methods: {
    submit () {
      this.$form.validate('#form', this, (error) => {
        if (!error) {
          this.$toast.show('校验成功')
        }
      })
    }
  }
}
</script>

<style>
.weui-toptips {
  top: 44px !important;
}
</style>



