<template>
  <div v-nav="{title: '手风琴'}">
    <accordion title="标题（默认不展开）" style="padding-top:10px;">
      <accordion-item type="list"><p>文字</p></accordion-item>
      <accordion-item type="list"><p>文字</p></accordion-item>
    </accordion>
    <accordion title="标题（设置展开）" :expanded="true">
      <accordion-item desc="说明文字" type="list"><p>文字</p></accordion-item>
      <accordion-item desc="说明文字" type="list"><p>文字</p></accordion-item>
    </accordion>
    <accordion title="内容为列表" :expanded="true" style="padding-top:10px;">
      <accordion-item desc="说明文字" type="list"><p>文字</p></accordion-item>
      <accordion-item desc="说明文字" type="list"><p>文字</p></accordion-item>
    </accordion>
    <accordion title="内容自定义" :expanded="true">
      <div style="padding:10px 15px;color:#ff0000;">内容</div>
    </accordion>
    <accordion title="带图标" :expanded="true" style="padding-top:10px;">
      <img slot="icon" width="20" style="display:block;margin-right:5px;" :src="img">
      <div style="padding:10px 15px;">内容</div>
    </accordion>
    <accordion :expanded="true">
      <span slot="header" style="color:#ff0000;">自定义标题</span>
      <div style="padding:10px 15px;">内容</div>
    </accordion>
  </div>
</template>

<script>
import { Accordion, AccordionItem } from '../components/accordion'

export default {
  components: {
    Accordion,
    AccordionItem
  },
  data () {
    return {
      img: 'data:image/png;base64,'
    }
  },
  methods: {
  }
}
</script>



