<template>
  <div v-nav="{title: '时间选择器'}">
    <div id="form">
      <group type="form" title="普通">
        <time-picker v-model="v1" title="出发时间" required placeholder="请选择出发时间"></time-picker>
      </group>
      <group type="form" title="自动填充">
        <time-picker v-model="v2" title="出发时间" auto-complete></time-picker>
      </group>
      <group type="form" title="默认值">
        <time-picker v-model="v3" title="出发时间"></time-picker>
      </group>
    </div>
    <div style="padding:30px 20px">
      <x-button color="primary" @click.native="submit">提交</x-button>
    </div>
  </div>
</template>

<script>
import Group from '../components/group'
import TimePicker from '../components/time-picker'
import XButton from '../components/button'

export default {
  components: {
    Group,
    TimePicker,
    XButton
  },
  data () {
    return {
      v1: '',
      v2: '',
      v3: '12:12'
    }
  },
  methods: {
    submit () {
      this.$form.validate('#form', this, (error) => {
        if (!error) {
          this.$toast.show('校验成功')
        }
      })
    }
  }
}
</script>

<style>
.weui-toptips {
  top: 44px !important;
}
</style>



