<template>
  <div v-nav="{title: '标签单选框'}">
    <div id="form">
      <group type="radio" title="普通">
        <label-radio v-model="v1" required :options="options" name="radio1" empty-tips="请选择性别"></label-radio>
      </group>
      <group type="radio" title="默认选中">
        <label-radio v-model="v2" :options="options" name="radio2" ></label-radio>
      </group>
      <group type="radio" title="不可选">
        <label-radio v-model="v3" required :options="options2" name="radio3" empty-tips="请选择学历"></label-radio>
      </group>
      <group type="radio" title="每行个数">
        <label-radio v-model="v4" :options="options2" name="radio4" :cols="4"></label-radio>
      </group>
      <group type="radio" title="激活颜色">
        <label-radio v-model="v5" :options="options" name="radio5" active-color="pink"></label-radio>
      </group>
    </div>
    <div style="padding:30px 20px">
      <x-button color="primary" @click.native="submit">提交</x-button>
    </div>
  </div>
</template>

<script>
import Group from '../components/group'
import LabelRadio from '../components/label-radio'
import XButton from '../components/button'

export default {
  components: {
    Group,
    LabelRadio,
    XButton
  },
  data () {
    return {
      options: [
        {label: '男', value: 'male'},
        {label: '女', value: 'female'}
      ],
      options2: [
        {label: '本科', value: '1'},
        {label: '硕士', value: '2'},
        {label: '博士', value: '3'},
        {label: '其他', value: '4', disabled: true}
      ],
      v1: '',
      v2: 'male',
      v3: '',
      v4: '',
      v5: 'female'
    }
  },
  methods: {
    submit () {
      this.$form.validate('#form', this, (error) => {
        if (!error) {
          this.$toast.show('校验成功')
        }
      })
    }
  }
}
</script>

<style>
.weui-toptips {
  top: 44px !important;
}
</style>



