<template>
  <div v-nav="{title: '开关项'}">
    <div id="form">
      <group type="form" title="普通（Boolean）">
        <x-switch v-model="v1" title="接收消息通知"></x-switch>
      </group>
      <div class="tips">选择值：{{d1}}</div>
      <group type="form" title="普通（Number）">
        <x-switch v-model="v2" title="接收消息通知"></x-switch>
      </group>
      <div class="tips">选择值：{{d2}}</div>
      <group type="form" title="不可用">
        <x-switch v-model="v3" title="声音" disabled></x-switch>
        <x-switch v-model="v4" title="振动"></x-switch>
      </group>
    </div>
  </div>
</template>

<script>
import Group from '../components/group'
import XSwitch from '../components/switch'

export default {
  components: {
    Group,
    XSwitch
  },
  data () {
    return {
      v1: true,
      v2: 0,
      v3: false,
      v4: 0,
      d1: 'true',
      d2: '0'
    }
  },
  watch: {
    v1 (val) {
      this.d1 = JSON.stringify(val)
    },
    v2 (val) {
      this.d2 = JSON.stringify(val)
    }
  }
}
</script>

<style scoped>
.tips {
  padding-left:15px;
  line-height:32px;
  color:#808080;
  font-size: 14px;
}
</style>



