<template>
  <div v-nav="{title: '列表'}">
    <list title="带说明列表">
      <list-item desc="说明文字"><p>标题文字</p></list-item>
    </list>
    <list title="IOS 风格列表（缩进）">
      <list-item desc="说明文字"><p>标题文字</p></list-item>
      <list-item desc="说明文字"><p>标题文字</p></list-item>
    </list>
    <list :indent="false" title="不缩进列表">
      <list-item desc="说明文字"><p>标题文字</p></list-item>
      <list-item desc="说明文字"><p>标题文字</p></list-item>
    </list>
    <list title="带图标列表">
      <list-item desc="说明文字">
        <p>标题文字</p>
        <div slot="icon">
          <img slot="icon" width="20" style="display:block;margin-right:5px;" :src="img">
        </div>
      </list-item>
    </list>
    <list title="链接列表">
      <list-item :link="{path: '/demo/list'}"><p>标题文字</p></list-item>
      <list-item desc="说明文字" :link="{path: '/demo/list'}"><p>标题文字</p></list-item>
      <list-item desc="说明文字" :link="{path: '/demo/list'}">
        <p>标题文字</p>
        <div slot="icon">
          <img slot="icon" width="20" style="display:block;margin-right:5px;" :src="img">
        </div>
      </list-item>
    </list>
    <list title="自定义标题">
      <list-item :link="{path: '/demo/list'}"><p>标题文字</p><p style="color:#aaa;">ddd</p></list-item>
      <list-item :link="{path: '/demo/list'}" :body-style="bdstyle"><p>标题文字</p></list-item>
    </list>
    <list title="自定义说明">
      <list-item :link="{path: '/demo/list'}" desc="说明文字"><p>标题文字</p><badge slot="desc" text="3"></badge></list-item>
      <list-item :link="{path: '/demo/list'}"><p>标题文字</p><badge slot="desc"></badge></list-item>
    </list>
    <list title="不带链接">
      <list-item desc="说明文字" @on-click="onClick()"><p>标题文字</p></list-item>
      <list-item desc="说明文字" :allow-access="false"><p>标题文字</p></list-item>
    </list>
  </div>
</template>

<script>
import { List, ListItem, Badge } from 'vue-weui-x'

export default {
  components: {
    List,
    ListItem,
    Badge
  },
  data () {
    return {
      img: 'data:image/png;base64,',
      bdstyle: {'color': '#09bb07'}
    }
  },
  methods: {
    onClick () {
      this.$toast.show({text: '不带链接,带事件', position: 'bottom', width: '200px'})
    }
  }
}
</script>



