<template>
  <div v-nav="{title: '确认框'}">
    <list :indent="false" title="组件模式">
      <list-item @on-click="showAlert1()">显示标题和内容</list-item>
      <list-item @on-click="showAlert2()">显示内容</list-item>
      <list-item @on-click="showAlert3()">确认/取消事件</list-item>
    </list>
    <list :indent="false" title="插件模式">
      <list-item @on-click="showAlert4()">显示标题和内容</list-item>
      <list-item @on-click="showAlert5()">显示内容</list-item>
      <list-item @on-click="showAlert6()">确认/取消事件</list-item>
    </list>
    <confirm v-model="show1" title="取消预约" content="您确认要取消预约吗？"></confirm>
    <confirm v-model="show2" content="您确认要取消预约吗？"></confirm>
    <confirm v-model="show3" @on-confirm="confirmEvent()" @on-cancel="cancelEvent()">您确认要取消预约吗？</confirm>
  </div>
</template>

<script>
import { List, ListItem } from '../components/list'
import Confirm from '../components/confirm'

export default {
  components: {
    List,
    ListItem,
    Confirm
  },
  data () {
    return {
      show1: false,
      show2: false,
      show3: false
    }
  },
  methods: {
    showAlert1 () {
      this.show1 = true
    },
    showAlert2 () {
      this.show2 = true
    },
    showAlert3 () {
      this.show3 = true
    },
    showAlert4 () {
      this.$confirm.show({title: '取消预约', content: '您确认要取消预约吗？'})
    },
    showAlert5 () {
      this.$confirm.show('您确认要取消预约吗？')
    },
    showAlert6 () {
      this.$confirm.show({content: '您确认要取消预约吗？', onConfirm: () => { this.confirmEvent() }, onCancel: () => { this.cancelEvent() }})
    },
    confirmEvent () {
      this.$toast.show('确定了')
    },
    cancelEvent () {
      this.$toast.show('取消了')
    }
  }
}
</script>



