<template>
  <div v-nav="{title: '轮播'}">
    <x-line>数据(props：list)</x-line>
    <carousel show-mask :list="data"></carousel>
    <x-line>数据(component:carousel-item)</x-line>
    <carousel show-mask>
      <carousel-item :img="carouselImg1" title="怀旧单车"></carousel-item>
      <carousel-item :img="carouselImg2" title="工匠精神"></carousel-item>
      <carousel-item :img="carouselImg3" title="萧条经济"></carousel-item>
    </carousel>
    <x-line>图片比例(props:aspect-ratio)</x-line>
    <carousel show-mask :list="data" :aspect-ratio="0.4" style="margin: 0 10px;"></carousel>
    <div style="margin-bottom:40px;"></div>
    <x-line>自动轮播(props:loop)</x-line>
    <div style="margin-bottom:10px;text-align:center;color:#999999;">事件 on-change: {{index}}</div>
    <carousel loop :list="data" dots-position="center" @on-change="changeEvn"></carousel>
    <x-line>设置初始位置(v-model)</x-line>
    <carousel v-model="current" loop :list="data" dots-position="left"></carousel>
    <x-line>点击事件(props:link)</x-line>
    <div style="margin-bottom:10px;text-align:center;color:#999999;">$e:on-click仅carousel-item支持</div>
    <carousel show-mask>
      <carousel-item :img="carouselImg1" title="怀旧单车" @on-click="onClick1"></carousel-item>
      <carousel-item :img="carouselImg2" title="工匠精神" @on-click="onClick2"></carousel-item>
      <carousel-item :img="carouselImg3" title="萧条经济" :link="{path:'/demo'}"></carousel-item>
    </carousel>
    <x-line>自定义</x-line>
    <carousel loop height="100px">
      <carousel-item><h2 class="title">它无孔不入</h2></carousel-item>
      <carousel-item><h2 class="title">你无处可藏</h2></carousel-item>
      <carousel-item><h2 class="title">不是它可恶</h2></carousel-item>
      <carousel-item><h2 class="title">而是它不懂你</h2></carousel-item>
      <carousel-item><h2 class="title">我们试图</h2></carousel-item>
      <carousel-item><h2 class="title">做些改变</h2></carousel-item>
    </carousel>
    <div style="margin-bottom:40px;"></div>
  </div>
</template>

<script>
import XLine from '../components/line'
import { Carousel, CarouselItem } from '../components/carousel'
import img1 from '../assets/carousel/1.jpg'
import img2 from '../assets/carousel/2.jpg'
import img3 from '../assets/carousel/3.jpg'

export default {
  components: {
    XLine,
    Carousel,
    CarouselItem
  },
  data () {
    return {
      carouselImg1: img1,
      carouselImg2: img2,
      carouselImg3: img3,
      data: [{img: img1, title: '怀旧单车'}, {img: img2, title: '工匠精神'}, {img: img3, title: '萧条经济'}],
      index: 0,
      current: 1
    }
  },
  methods: {
    changeEvn (index) {
      this.index = index
    },
    onClick1 () {
      this.$toast.show('怀旧单车')
    },
    onClick2 () {
      this.$toast.show('工匠精神')
    }
  }
}
</script>

<style scoped>
.title{
  line-height: 100px;
  text-align: center;
  color: #fff;
  background-color: #000;
}
</style>



