<template>
  <div v-nav="{title: '输入框'}">
    <div id="form">
      <group type="form" title="普通">
        <x-input title="姓名" placeholder="请输入您的姓名" required></x-input>
      </group>
      <group type="form" title="只读/不可用">
        <x-input v-model="model1" title="所在地区" placeholder="请输入您的所在地区" readonly></x-input>
        <x-input v-model="model2" title="详细地址" placeholder="请输入您的详细地址" disabled></x-input>
      </group>
      <group type="form" title="正则验证">
        <x-input type="tel" title="手机号" pattern="^\d{11}$" placeholder="请输入您的手机号" not-match-tips="请输入正确的手机号" :max-length="11"></x-input>
        <x-input title="身份证号" pattern="/(?:^\d{15}$)|(?:^\d{18}$)|^\d{17}[\dXx]$/" placeholder="请输入您的身份证号" not-match-tips="请输入正确的身份证号"></x-input>
      </group>
      <group type="form" title="change事件">
        <x-input v-model="model3" title="订单号" placeholder="请输入您的订单号" @on-change="change"></x-input>
        <x-input v-model="model4" title="订单号" readonly></x-input>
      </group>
      <group type="form" title="focus事件">
        <x-input type="number" title="商品单价" placeholder="请输入您的商品单价" @on-focus="focus"></x-input>
      </group>
      <group type="form" title="无标题">
        <x-input placeholder="请输入备注信息" ></x-input>
      </group>
      <group type="form" title="验证码">
        <x-input type="tel" title="手机号" pattern="^\d{11}$" placeholder="请输入您的手机号" not-match-tips="请输入正确的手机号" :max-length="11" required>
          <button class="vcode-btn" slot="right">获取验证码</button>
        </x-input>
        <x-input title="验证码" placeholder="请输入验证码" pattern="^[A-Za-z0-9]{4}$" :max-length="4" required empty-tips="请输入验证码！">
          <img class="vcode" :src="vcode" slot="right">
        </x-input>
      </group>
      <group type="form" title="标签样式(长度控制示例)" labelStyle="width:120px">
        <x-input title="父组件控制" placeholder="input组件label样式"></x-input>
        <x-input title="子组件控制" placeholder="input组件label样式" labelStyle="width:100px;"></x-input>
      </group>
      <group type="form" title="输入框样式" inputStyle="color:#f43530;">
        <x-input v-model="model5" title="父组件控制" placeholder="input组件label样式"></x-input>
        <x-input v-model="model6" title="子组件控制" placeholder="input组件label样式" inputStyle="text-align:right"></x-input>
      </group>
      <group type="form" title="其他">
        <x-input v-model="model7" title="卡号" placeholder="请扫一扫" readonly>
          <button class="scale" slot="right" @click="click"><icon name="qrcode" :scale="1.3"></icon></button>
        </x-input>
      </group>
    </div>
    <div style="padding:30px 20px">
      <x-button color="primary" @click.native="submit">提交</x-button>
    </div>
  </div>
</template>

<script>
import Group from '../components/group'
import XInput from '../components/input'
import Icon from '../components/icon'
import XButton from '../components/button'

export default {
  components: {
    Group,
    XInput,
    Icon,
    XButton
  },
  data () {
    return {
      model1: '广东省广州市',
      model2: '荔湾区芳村花海街道',
      model3: '',
      model4: '',
      model5: '字体颜色',
      model6: '显示方向',
      model7: '',
      vcode: 'data:image/jpeg;base64,'
    }
  },
  methods: {
    change (val) {
      this.model4 = val
    },
    focus () {
      this.$toast.show('焦点了')
    },
    click () {
      this.model7 = Math.random().toString(36).substring(3, 15).toUpperCase()
    },
    submit () {
      this.$form.validate('#form', this, (error) => {
        if (!error) {
          this.$toast.show('校验成功')
        }
      })
    }
  }
}
</script>

<style scoped>
.vcode-btn {
  background-color: transparent;
  border-top: 0;
  border-right: 0;
  border-bottom: 0;
  outline: 0;
  display: inline-block;
  height: 45px;
  margin-left: 5px;
  padding: 0 0.7em;
  border-left: 1px solid #E5E5E5;
  line-height: 45px;
  vertical-align: middle;
  font-size: 17px;
  color: #09BB07;
}
.vcode {
  margin-left: 5px;
  height: 45px;
  vertical-align: middle;
}
.scale {
  background-color: transparent;
  border-top: 0;
  border-right: 0;
  border-bottom: 0;
  outline: 0;
  display: inline-block;
  height: 45px;
  margin-left: 5px;
  padding: 0.6em 0.7em 0 0.7em;
  border-left: 0;
  line-height: 45px;
}
</style>

<style>
.weui-toptips {
  top: 44px !important;
}
</style>



