<template>
  <div v-nav="{title: '评分'}">
    <list title="仅显示">
      <list-item :allow-access="false"><span>整分值</span><rater v-model="rate1" slot="desc" :activated="false"></rater></list-item>
      <list-item :allow-access="false" ><span>半分值</span><rater v-model="rate2" slot="desc" :activated="false"></rater></list-item>
    </list>
    <list title="可评分">
      <list-item :allow-access="false"><span>整分值</span><rater v-model="rate3" slot="desc"></rater></list-item>
      <list-item :allow-access="false" ><span>半分值</span><rater v-model="rate4" :half="true" slot="desc"></rater></list-item>
    </list>
  </div>
</template>

<script>
import { List, ListItem } from '../components/list'
import Rater from '../components/rater'

export default {
  components: {
    List,
    ListItem,
    Rater
  },
  data () {
    return {
      rate1: 4,
      rate2: 2.5,
      rate3: 4,
      rate4: 0.5
    }
  }
}
</script>



