<template>
  <div v-nav="{title: '标签复选框'}">
    <div id="form">
      <group type="checkbox" title="普通">
        <label-checkbox v-model="v1" required :options="options"  name="checkbox" empty-tips="请选择A或B"></label-checkbox>
      </group>
      <div class="tips">选择值：{{displayV}}</div>
      <group type="checkbox" title="默认选中">
        <label-checkbox v-model="v2" :options="options" name="checkbox2" ></label-checkbox>
      </group>
      <group type="checkbox" title="选择个数（1-2）">
        <label-checkbox v-model="v3" :options="options2" pattern="{1,2}" name="checkbox3" not-match-tips="请选择1-2个城市"></label-checkbox>
      </group>
      <group type="checkbox" title="每行个数">
        <label-checkbox v-model="v4" :options="options3" name="checkbox4" :cols="4"></label-checkbox>
      </group>
      <group type="checkbox" title="激活颜色">
        <label-checkbox v-model="v5" :options="options" name="checkbox5" active-color="pink"></label-checkbox>
      </group>
    </div>
    <div style="padding:30px 20px">
      <x-button color="primary" @click.native="submit">提交</x-button>
    </div>
  </div>
</template>

<script>
import Group from '../components/group'
import LabelCheckbox from '../components/label-checkbox'
import XButton from '../components/button'

export default {
  components: {
    Group,
    LabelCheckbox,
    XButton
  },
  data () {
    return {
      options: [
        {label: 'A', value: 'a'},
        {label: 'B', value: 'b'}
      ],
      options2: [
        {label: '北京', value: '1'},
        {label: '上海', value: '2'},
        {label: '广州', value: '3'},
        {label: '其他', value: '4', disabled: true}
      ],
      options3: [
        {label: '苹果', value: '1'},
        {label: '华为', value: '2'},
        {label: '小米', value: '3'},
        {label: '三星', value: '4'}
      ],
      v1: [],
      v2: ['a', 'b'],
      v3: [],
      v4: [],
      v5: ['a'],
      displayV: '[]'
    }
  },
  methods: {
    submit () {
      this.$form.validate('#form', this, (error) => {
        if (!error) {
          this.$toast.show('校验成功')
        }
      })
    }
  },
  watch: {
    v1 (val) {
      this.displayV = JSON.stringify(val)
    }
  }
}
</script>

<style scoped>
.tips {
  padding-left:15px;
  line-height:32px;
  color:#808080;
  font-size: 14px;
}
</style>

<style>
.weui-toptips {
  top: 44px !important;
}
</style>



