<template>
  <div v-nav="{title: '日期选择器'}">
    <div id="form">
      <group type="form" title="普通">
        <date-picker v-model="v1" title="有效日期"></date-picker>
      </group>
      <group type="form" title="起止年份">
        <date-picker v-model="v2" title="有效日期" :start="2016" :end="2018" required placeholder="请选择有效日期"></date-picker>
      </group>
      <group type="form" title="自动填充">
        <date-picker v-model="v3" title="有效日期" auto-complete></date-picker>
      </group>
      <group type="form" title="默认值">
        <date-picker v-model="v4" title="有效日期"></date-picker>
      </group>
    </div>
    <div style="padding:30px 20px">
      <x-button color="primary" @click.native="submit">提交</x-button>
    </div>
  </div>
</template>

<script>
import Group from '../components/group'
import DatePicker from '../components/date-picker'
import XButton from '../components/button'

export default {
  components: {
    Group,
    DatePicker,
    XButton
  },
  data () {
    return {
      v1: '',
      v2: '',
      v3: '',
      v4: '2018/1/1'
    }
  },
  methods: {
    submit () {
      this.$form.validate('#form', this, (error) => {
        if (!error) {
          this.$toast.show('校验成功')
        }
      })
    }
  }
}
</script>

<style>
.weui-toptips {
  top: 44px !important;
}
</style>



