<template>
  <div v-nav="{title: '提示框'}">
    <list :indent="false" title="组件模式">
      <list-item @on-click="showAlert1()">显示标题和内容</list-item>
      <list-item @on-click="showAlert2()">显示内容</list-item>
      <list-item @on-click="showAlert3()">确认事件</list-item>
    </list>
    <list :indent="false" title="插件模式">
      <list-item @on-click="showAlert4()">显示标题和内容</list-item>
      <list-item @on-click="showAlert5()">显示内容</list-item>
      <list-item @on-click="showAlert6()">确认事件</list-item>
    </list>
    <alert v-model="show1" title="提醒" content="删除成功！"></alert>
    <alert v-model="show2" content="删除成功！"></alert>
    <alert v-model="show3" @on-hide="hideEvent()">删除成功！</alert>
  </div>
</template>

<script>
import { List, ListItem } from '../components/list'
import Alert from '../components/alert'

export default {
  components: {
    List,
    ListItem,
    Alert
  },
  data () {
    return {
      show1: false,
      show2: false,
      show3: false
    }
  },
  methods: {
    showAlert1 () {
      this.show1 = true
    },
    showAlert2 () {
      this.show2 = true
    },
    showAlert3 () {
      this.show3 = true
    },
    showAlert4 () {
      this.$alert.show({title: '提醒', content: '删除成功！'})
    },
    showAlert5 () {
      this.$alert.show('删除成功！')
    },
    showAlert6 () {
      this.$alert.show({content: '删除成功！', onHide: () => { this.hideEvent() }})
    },
    hideEvent () {
      this.$toast.show('确认了')
    }
  }
}
</script>



