<template>
  <div v-nav="{title: '弹出式菜单'}">
    <list :indent="false" title="ios 模式">
      <list-item @on-click="show1 = true">常规</list-item>
      <list-item @on-click="show2 = true">显示标题</list-item>
      <list-item @on-click="show3 = true">不显示取消菜单</list-item>
      <list-item @on-click="show4 = true">禁止点击Mask关闭</list-item>
      <list-item @on-click="show5 = true">自定义标题</list-item>
      <list-item @on-click="show6 = true">自定义菜单</list-item>
      <list-item @on-click="show7 = true">菜单事件</list-item>
    </list>
    <list :indent="false" title="android 模式">
      <list-item @on-click="show8 = true">仅菜单、无标题和取消菜单</list-item>
    </list>
    <action-sheet v-model="show1">
      <action-sheet-item>拍照</action-sheet-item>
      <action-sheet-item>从相册选择</action-sheet-item>
      <action-sheet-item>其他</action-sheet-item>
    </action-sheet>
    <action-sheet v-model="show2" title="选择图片分享">
      <action-sheet-item>拍照</action-sheet-item>
      <action-sheet-item>从相册选择</action-sheet-item>
      <action-sheet-item>其他</action-sheet-item>
    </action-sheet>
    <action-sheet v-model="show3" title="选择图片分享" :show-cancel="false">
      <action-sheet-item>拍照</action-sheet-item>
      <action-sheet-item>从相册选择</action-sheet-item>
      <action-sheet-item>其他</action-sheet-item>
    </action-sheet>
    <action-sheet v-model="show4" :hide-on-clicking-mask="false">
      <action-sheet-item>拍照</action-sheet-item>
      <action-sheet-item>从相册选择</action-sheet-item>
      <action-sheet-item>其他</action-sheet-item>
    </action-sheet>
    <action-sheet v-model="show5">
      <div slot="header">
          <span style="color:#000;font-size:18px;line-height:32px;">删除确认</span>
          <p>删除后将无法恢复</p>
      </div>
      <action-sheet-item>确认</action-sheet-item>
    </action-sheet>
    <action-sheet v-model="show6">
      <action-sheet-item style="color:#ff0000">删除</action-sheet-item>
    </action-sheet>
    <action-sheet v-model="show7">
      <action-sheet-item @on-click="menu1()">拍照</action-sheet-item>
      <action-sheet-item @on-click="menu2()">从相册选择</action-sheet-item>
      <action-sheet-item>其他</action-sheet-item>
    </action-sheet>
    <action-sheet v-model="show8" theme="android">
      <action-sheet-item>拍照</action-sheet-item>
      <action-sheet-item>从相册选择</action-sheet-item>
      <action-sheet-item>其他</action-sheet-item>
    </action-sheet>
  </div>
</template>

<script>
import { List, ListItem } from '../components/list'
import { ActionSheet, ActionSheetItem } from '../components/action-sheet'

export default {
  components: {
    List,
    ListItem,
    ActionSheet,
    ActionSheetItem
  },
  data () {
    return {
      show1: false,
      show2: false,
      show3: false,
      show4: false,
      show5: false,
      show6: false,
      show7: false,
      show8: false
    }
  },
  methods: {
    menu1 () {
      this.$toast.show('选择拍照')
    },
    menu2 () {
      this.$loading.show('加载中')
      setTimeout(() => {
        this.$loading.hide()
      }, 2000)
    }
  }
}
</script>



