<template>
  <div v-nav="{title: 'Marquee'}">
    <x-line>常规</x-line>
    <marquee class="border">
      <marquee-item class="center item">它无孔不入</marquee-item>
      <marquee-item class="center item">你无处可藏</marquee-item>
      <marquee-item class="center item">不是它可恶</marquee-item>
      <marquee-item class="center item">而是它不懂你</marquee-item>
      <marquee-item class="center item">我们试图</marquee-item>
      <marquee-item class="center item">做些改变</marquee-item>
    </marquee>
    <x-line>自定义</x-line>
    <marquee style="margin:0 20px; box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);">
      <marquee-item class="item2">
        <img width="50" style="display:block;float:left;" :src="sina">
        <div style="padding-left:55px;"><p>新浪微博</p><p class="text">新浪微博,随时随地发现新鲜事！</p></div>
      </marquee-item>
      <marquee-item class="item2">
        <img width="50" style="display:block;float:left;" :src="webchat">
        <div style="padding-left:55px;"><p>微信</p><p class="text">微信,是一个生活方式！</p></div>
      </marquee-item>
    </marquee>
    <x-line>异步</x-line>
    <marquee async class="border" v-if="count > 0">
      <marquee-item v-for="i in count" :key="i" class="center item">{{i}}. VWX based on Vue and WeUI</marquee-item>
    </marquee>
  </div>
</template>

<script>
import XLine from '../components/line'
import { Marquee, MarqueeItem } from '../components/marquee'
import SINAIcon from '../assets/media/popup_sina.png'
import WebchatIcon from '../assets/media/popup_wechat.png'

export default {
  components: {
    XLine,
    Marquee,
    MarqueeItem
  },
  data () {
    return {
      count: 0,
      sina: SINAIcon,
      webchat: WebchatIcon
    }
  },
  mounted () {
    setTimeout(() => {
      this.count = 4
    }, 1000)
  }
}
</script>

<style scoped>
.border{
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
.center {
  text-align: center;
}
.item {
  height: 34px;
  line-height: 34px;
  vertical-align: middle;
}
.item2 {
  vertical-align: middle;
  padding: 5px;
  background-color: #fff;
  border-radius: 4px;
}
.text {
  width: 240px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
  color: #aaa;
}
</style>



