<template>
  <div v-nav="{title: '选择器'}">
    <div id="form">
      <group type="form" title="普通">
        <x-select v-model="dv1" :data="data1" title="证件类型" placeholder="请选择证件类型" required></x-select>
      </group>
      <span style="padding-left:15px;line-height:30px;">所选项值为：{{dv1}}</span>
      <group type="form" title="无标题">
        <x-select v-model="dv2" :data="data1" placeholder="请选择证件类型"></x-select>
      </group>
      <group type="form" title="默认值">
        <x-select v-model="dv3" :data="data1"  title="证件类型" placeholder="请选择证件类型" :defaultDispalyText="dl3"></x-select>
      </group>
      <group type="form" title="级联多列(porp:depth=3)">
        <x-select v-model="dv4" :data="data2"  title="地区" placeholder="请选择地区" :defaultValue="[0,0,1]" :depth="3" @on-select="onselect" :defaultDispalyText="dv4"></x-select>
      </group>
      <p style="padding-left:15px;line-height:30px;color:#aaa;">注：级联选择需通过on-select事件进行赋值</p>
      <group type="form" title="自定义选择（event:on-select）">
        <x-select title="到达地点" placeholder="你要去哪儿？" @on-select="onselect2"></x-select>
      </group>
    </div>
    <div style="padding:30px 20px">
      <x-button color="primary" @click.native="submit">提交</x-button>
    </div>
  </div>
</template>

<script>
import Group from '../components/group'
import XSelect from '../components/select'
import XButton from '../components/button'

export default {
  components: {
    Group,
    XSelect,
    XButton
  },
  data () {
    return {
      data1: [
        {label: '身份证', value: 0},
        {label: '学生证', value: 1},
        {label: '军官证', value: 2},
        {label: '护照', value: 3},
        {label: '其他', value: 4}
      ],
      data2: [{
        label: '广东',
        value: 0,
        children: [{
          label: '广州',
          value: 0,
          children: [{
            label: '海珠',
            value: 0
          }, {
            label: '番禺',
            value: 1
          }]
        }, {
          label: '佛山',
          value: 1,
          children: [{
            label: '禅城',
            value: 0
          }, {
            label: '南海',
            value: 1
          }]
        }]
      },
      {
        label: '广西',
        value: 1,
        children: [{
          label: '南宁',
          value: 0,
          children: [{
            label: '青秀',
            value: 0
          }, {
            label: '兴宁',
            value: 1
          }]
        }, {
          label: '桂林',
          value: 1,
          children: [{
            label: '象山',
            value: 0
          }, {
            label: '秀峰',
            value: 1
          }]
        }]
      }],
      dv1: '',
      dv2: '',
      dv3: 3,
      dl3: '护照',
      dv4: ''
    }
  },
  methods: {
    onselect (obj) {
      this.dv4 = obj[0].label + obj[1].label + obj[2].label
    },
    onselect2 () {
      this.$toast.show('自定义选择')
    },
    submit () {
      this.$form.validate('#form', this, (error) => {
        if (!error) {
          this.$toast.show('校验成功')
        }
      })
    }
  }
}
</script>

<style>
.weui-toptips {
  top: 44px !important;
}
</style>



